import React, { Component } from 'react';
import data from '../data.json';
import './AboutSection.css';

import Fullpage from '../main/Fullpage';
import { Link, Element } from 'react-scroll';

export default class AboutSection extends Component {
	render() {
		return (
			<div>
				<Fullpage class="about">
					<div>
						<h1 className="about-title">{data.sections[0].title}</h1>
						{data.sections[0].items.map(el => {
							return <p className="about-p">{el.content}</p>
						})}
					</div>

					<Link
						activeClass="active"
						to="about"
						spy={true}
						smooth={true}
						offset={50}
						duration={500}
						onSetActive={this.handleSetActive}>
						<div style={{
							position: 'relative',
							backgroundColor: 'rgb(255, 208, 0);',
							bottom: '-600%'
						}}>
							<img src={data.icons.down} style={{
								width: '30px'
							}} />
						</div>
					</Link>
				</Fullpage>
				<Element name="about" className="element">

				</Element>
			</div>
		)
	}
}